<template>
  <view>
    <view class="order-list">
      <uv-tabs
        :list="tabs"
        customStyle="width: 100%;height: 80rpx;"
        itemStyle="width: 50%;height: 80rpx;box-sizing: border-box"
        @click="changeTabs"
        lineColor="#303133"
      ></uv-tabs>
      <view class="main-box">
        <view class="list-item-box" v-for="(item, index) in list" :key="index">
          <ServiceStyleItem
            :item="item"
            :is-detail="true"
            :is-rated="isRated"
            :is-comment="true"
            @like="onLike"
            @comment="onComment"
          />
        </view>
      </view>
      <empty v-if="showEmpty"></empty>
      <loadMore :status="loadStatus" v-if="showLoadMore" />
    </view>
  </view>
</template>

<script>
import shareMixins from '@/mixins/shareMixins'
import ServiceStyleItem from '../components/ServiceStyleItem/index.vue'
import homeDropDown from './components/homeDropDown.vue'
import monthCard from './components/monthCard.vue'
import listMixins from '@/mixins/listMixins.js'
import { getMonthStartAndEnd, formatTimeBase } from '@/utils/timeUtil'
import { getMyOderList } from '@/apis/sportsService/serviceStyle'
export default {
  mixins: [shareMixins, listMixins],
  components: {
    ServiceStyleItem,
    homeDropDown,
    monthCard,
  },
  data() {
    return {
      tabs: [
        { name: '待评价', count: 3, key: 'rating_wait' },
        { name: '全部', count: 8, key: '' },
      ],
      searchObj: {},
      list: [],
      scrollTop: 0,
      count: 0,
      timeObj: {
        start_time: '',
        end_time: '',
      },
      activityType: '',
      serveWorkDevStatus: 'rating_wait',
    }
  },
  onLoad(options) {
    if (options.activityType) {
      this.activity_type = options.activityType
    }
  },
  onShow() {
    // this.getSearchTime()
    this.resetData()
  },
  methods: {
    onChangeMonthData(data) {
      console.log('[ data ] >', data)
      this.getSearchTime(data.year, data.month)
      // this.getList()
      this.resetData()
    },
    getSearchTime(year, month) {
      if (!year || !month) {
        let now = new Date()
        year = now.getFullYear()
        month = now.getMonth() + 1
      }
      let { start, end } = getMonthStartAndEnd(year, month)
      this.timeObj = {
        start_time: formatTimeBase(start, '{y}-{m}-{d}'),
        end_time: formatTimeBase(end, '{y}-{m}-{d}'),
      }
    },
    search(data) {
      this.searchObj = data
      this.resetData()
    },
    getDataDemo() {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve({
            data: [
              {
                id: 1,
                name: '活动名称',
                time: '2022-01-01',
                address: '活动地点',
                type: '活动类型',
                status: '活动状态',
              },
            ],
            total: 10,
          })
        }, 1000)
      })
    },
    async getList(refresh = false) {
      let params = {
        page: this.page,
        size: 10,
        serveWorkDevStatus: this.serveWorkDevStatus,
      }
      // let res = await this.getDataDemo(params)
      const res = await getMyOderList(params)
      console.log({ res })
      if (res.code === 0) {
        console.log(this.$refs.pagingRef)
        res.data.listData.forEach((item) => {
          console.log({ item })
          item.images = item.image?.split(',')
        })
        // console.log({ data });
        // this.$refs.pagingRef?.complete(data)
        this.getListExtend(res, refresh)
      }
    },
    // 切换tab栏
    changeTabs(e) {
      console.log({ e })
      this.serveWorkDevStatus = e.key
      this.resetData()
    },
  },
}
</script>

